.graph-wrapper {
  position: relative;
}

.graph-d3 {
  border: 1px solid #ccc;
  margin: 0 1rem;
}

#graph-selector {
  background: #ca205e80;
  border: 1px dotted #000;
  position: fixed;
}

#nodes-selection {
  max-width: 600px;
  z-index: 1;
  max-height: 600px;
  background-color: #fff;
  margin: auto;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 5px 15px #0000000d;
}

#nodes-selection .ro-icon-close {
  margin: 10px;
  position: absolute;
  right: 0;
}

#nodes-selection form {
  margin: 72px;
}

.graph-overlay {
  height: auto;
  min-height: 50%;
  width: 320px;
  word-wrap: break-word;
  background: #fff;
  border: 1px solid #ccc;
  padding: .5rem;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.graph-overlay--centered {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.node {
  cursor: pointer;
}

.node text {
  max-width: 50px;
}

.node circle {
  fill: #fff;
  stroke: #4682b4;
  stroke-width: 3px;
}

.node text {
  font: 12px sans-serif;
}

.link {
  fill: none;
  stroke: #ccc;
  stroke-width: 2px;
}

